import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {LibraryService} from '../../../service/library/library.service';
import {ClassService} from '../../../service/academic/class.service';
import {LangandparmisionService} from '../../../service/langandparmision.service';

@Component({
  selector: 'app-ebooks',
  templateUrl: './ebooks.page.html',
  styleUrls: ['./ebooks.page.scss'],
})
export class EbooksPage implements OnInit {

    ebooks: any;
    permission: any;
    language: any;

    constructor(
        private router: Router,
        private route: ActivatedRoute,
        public libraryService: LibraryService,
        public langandpermissionService: LangandparmisionService,
        public classService: ClassService
    ) { this.getBooks(); }


    ngOnInit() {
    }


    getBooks() {
        this.libraryService.getEBooks()
            .then((data: any) => {
                this.ebooks = data.ebooks;
            });

        this.langandpermissionService.getLangandPermissionCall(false, 'ebooks')
            .then((data: any) => {
                this.permission = data.permission;
                this.language = data.language;
            });
    }

}
